}
.progress{
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.percent-text{
    font-family: 'Josefin Sans', sans-serif;
    color: white;
}
